/* This stylesheet is used to style the public-facing components of the plugin. */
/*** Poverty Report ***/
.report-processing-msg {
  display: none;
  color: #f00;
  font-weight: 700;
  font-size: 1.2em;
}
#nyscaa-report-selection {
  margin-bottom: 20px;
}
#nyscaa-report-selection select {
  margin-left: 20px;
}
#nyscaa-report-selection .nyscaa-report-type {
  margin-bottom: 20px;
}
#nyscaa-report-selection .nyscaa-report-type a {
  cursor: pointer;
}
#nyscaa-report-content {
  clear: both;
  font-size: 100%;
  margin-bottom: 20px;
  word-wrap: normal;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  hyphens: none;
}
#nyscaa-report-content p {
  margin-bottom: 15px;
  line-height: 1.4em;
}
#nyscaa-report-content a {
  color: #002395;
}
#nyscaa-report-content table,
#nyscaa-report-content td {
  border: 0;
  margin-bottom: 0;
  line-height: 1;
}
#nyscaa-report-content div,
#nyscaa-report-content span {
  line-height: 150%;
}
#nyscaa-report-content .quarter-block,
#nyscaa-report-content .third-block {
  margin-bottom: 0;
  padding-bottom: 0;
}
#nyscaa-report-content .center-align {
  text-align: center;
}
#nyscaa-report-content .font-bold {
  font-weight: 700;
}
#nyscaa-report-content .section-spacing {
  clear: both;
  padding-top: 13px;
}
#nyscaa-report-content .dark-blue-text {
  color: #002395;
  font-weight: bold;
}
#nyscaa-report-content .light-blue-text {
  color: #00B0F0;
  font-weight: bold;
}
#nyscaa-report-content .clear-float {
  clear: both;
}
#nyscaa-report-content #nyscaa-report-datakey p {
  font-size: 13px;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-left {
  float: left;
  padding-right: 10px;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-right {
  float: left;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-title {
  float: left;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datenote {
  float: left;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-title1 {
  font-family: Impact, sans-serif;
  font-size: 25pt;
  font-weight: 700;
  color: #002395;
  text-align: center;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-title2 {
  margin-top: 18px;
  text-align: center;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-censusbox {
  border: 1px solid #002395;
  color: #00B0F0;
  padding: 5px;
  font-size: 12pt;
  font-weight: 500;
  text-align: center;
  margin-bottom: 10px;
}
#nyscaa-report-content #nyscaa-report-datakey .nyscaa-report-datatitle {
  font-size: 18pt;
  font-weight: 500;
  color: #002395;
  font-family: Impact, sans-serif;
  line-height: 1.2em;
  letter-spacing: 1px;
}
#nyscaa-report-content #nyscaa-report-datakey .nyscaa-report-datatitle2 {
  font-size: 12pt;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-edupov {
  padding: 0 35px;
  display: inline-block;
  vertical-align: middle;
  white-space: nowrap;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-2,
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-3 p,
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-5 {
  clear: both;
}
#nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-2 p {
  margin-top: 10px;
}
#nyscaa-report-content #nyscaa-report-datakey .nyscaa-report-datakey-note {
  color: #002395;
  font-weight: bold;
}
#nyscaa-report-content #nyscaa-report-infograph .quarter {
  width: 100%;
}
#nyscaa-report-content #nyscaa-report-infograph .font-mid {
  font-size: 13pt;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-top {
  color: #002395;
  font-size: 12pt;
  font-weight: 500;
  margin-bottom: 10px;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-map img {
  max-width: 250px;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-popnote {
  font-style: italic;
  font-size: 10pt;
  text-align: center;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-pop-age b {
  margin-left: 30px;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-povrate {
  color: #002395;
  font-weight: 700;
  font-size: 50pt;
  font-family: 'Times New Roman';
  text-shadow: -2px 2px #bbb;
  text-align: center;
  line-height: 100%;
  margin-top: 15px;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-geoglevel {
  text-align: center;
  color: #00B0F0;
  font-size: 32pt;
  line-height: 100%;
  font-weight: 700;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-poverty,
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-poverty-rate {
  color: #27AAE1;
  font-weight: 700;
  font-size: 35pt;
  text-align: center;
  font-family: Impact, sans-serif;
  line-height: 100%;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-poverty-rate {
  letter-spacing: 10px;
  font-size: 42pt;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-living b {
  font-size: 13pt;
  font-weight: 700;
  margin: 10px 0;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-living p {
  clear: left;
  margin: 10px 0 0;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-living .nyscaa-report-v1 {
  width: 40%;
  padding-left: 30px;
  float: left;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left #nyscaa-report-living .nyscaa-report-v2 {
  width: 40%;
  float: left;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-right {
  display: inline-block;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-right #nyscaa-report-footer-povrate {
  font-size: 12px;
  padding-top: 10px;
}
#nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-right #nyscaa-report-footer-povrate img {
  width: 5px;
  padding: 0 5px 5px 5px;
  vertical-align: middle;
}
#nyscaa-report-content #nyscaa-report-infograph .nyscaa-report-content-title {
  font-family: Impact, sans-serif;
  font-size: 25pt;
  font-weight: 700;
  color: #002395;
  line-height: 1.3;
}
#nyscaa-report-content #nyscaa-report-infograph .nyscaa-report-content-title2 {
  font-size: 20pt;
  font-weight: 700;
  color: #002395;
  font-family: Impact, sans-serif;
  line-height: 1.2em;
  letter-spacing: 1px;
}
#nyscaa-report-content #nyscaa-report-infograph .nyscaa-report-content-title3 {
  font-size: 14pt;
  font-weight: 500;
  color: #002395;
  font-family: Impact, sans-serif;
}
#nyscaa-report-content #nyscaa-report-infograph .nyscaa-report-content-name {
  color: #002395;
  padding: 1px 0;
  font-weight: 700;
}
#nyscaa-report-content #nyscaa-report-infograph .nyscaa-report-content-name-light {
  color: #00B0F0;
  padding: 3px 0;
  font-weight: 700;
}
#nyscaa-report-content #nyscaa-report-infograph .nyscaa-report-content-value {
  padding: 1px 0;
  font-weight: 700;
}
#nyscaa-report-content #nyscaa-report-infograph .nyscaa-report-legend-dark {
  border: 1px solid #000;
  background-color: #002395;
  font-size: 10px;
  padding: 0 5px;
}
#nyscaa-report-content #nyscaa-report-infograph .nyscaa-report-legend-light {
  font-size: 10px;
  padding: 0 5px;
  border: 1px solid #000;
  background-color: #00B0F0;
}
#nyscaa-report-content #nyscaa-report-infograph .padding-left10 {
  padding-left: 10px;
}
#nyscaa-report-content #nyscaa-report-infograph .gray-border {
  border-top: 10px solid #ddd;
}
@media all and (min-width: 600px) {
  #nyscaa-report-content #nyscaa-report-infograph .quarter {
    width: 49.5%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-emp #emp-part1 {
    width: 25%;
    float: left;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-emp #emp-part2 {
    float: left;
    width: 74.9%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-emp #emp-part2 #emp-part2-a {
    float: left;
    width: 50%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-emp #emp-part2 #emp-part2-b {
    float: right;
    width: 200px;
    margin-top: 45px;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-health #health-part1 {
    width: 25%;
    float: left;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-health #health-part2 {
    width: 74.9%;
    float: left;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-health #health-part2 #health-part2-a {
    float: left;
    width: 45%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-health #health-part2 #health-part2-b {
    float: right;
    width: 54%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-gender #gender-part1 {
    float: left;
    width: 60%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-gender #gender-part1 #gender-part1-a {
    float: left;
    width: 49%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-gender #gender-part1 #gender-part1-b,
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-gender #gender-part1 #gender-part1-c {
    float: left;
    width: 25%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-gender #gender-part2 {
    float: right;
    width: 35%;
  }
}
@media all and (min-width: 750px) {
  #nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-left {
    width: 26%;
    padding-right: 10px;
  }
  #nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-right {
    border-left: 5px solid #00B0F0;
    width: 70%;
    float: left;
    padding-left: 10px;
  }
  #nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-title {
    width: 30%;
  }
  #nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datenote {
    width: 65%;
  }
  #nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-wagekey {
    float: left;
    width: 42%;
    padding: 10px 20px 0 0;
  }
  #nyscaa-report-content #nyscaa-report-datakey #nyscaa-report-datakey-41 {
    float: left;
    width: 54%;
  }
  #nyscaa-report-content #nyscaa-report-infograph .quarter {
    width: 25%;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-top {
    float: right;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-left {
    border-right: 20px solid #00B0F0;
    width: 22.9%;
    padding-right: 2%;
    float: left;
    clear: both;
  }
  #nyscaa-report-content #nyscaa-report-infograph #nyscaa-report-content-right {
    width: 71%;
    float: left;
    padding-left: 10px;
  }
}
#nyscaa-report-content #nyscaa-report-infograph .quarter {
  float: left;
}
.nyscaa-report-action {
  clear: both;
  margin-bottom: 20px;
}
.nyscaa-report-action hr {
  margin-top: 20px;
}
